import React from 'react'
import Nav from './Nav'
import {Link} from 'react-router'

class Layout extends React.Component {
render(){
  return(
  
    <div>
    <Nav/>
    {this.props.children}
    <Link to = "/home">Home</Link>
    </div>

    )
}
}

export default Layout


